import React from "react";
import persona from "../data/persona3p_personae";
import { FusionCalculator } from "../personaUtils";
import { arcana2Combos, arcana3Combos, specialCombos } from "../data/persona3p_fusion";

const calculator = new FusionCalculator(persona, arcana2Combos, arcana3Combos, specialCombos)
// const ps = [calculator.getPersona('Pyro Jack'), calculator.getPersona('Unicorn'), calculator.getPersona('Orpheus')]
// let p = calculator.getTriangleRecipes('Fool').sort((recipe1, recipe2) => recipe1.result.level - recipe2.result.level)
// console.log('Result: ');
// arcana rank
// console.log(Object.entries(calculator.arcanaRank).sort((a, b) => a - b));
// console.log(...ps);

class PersonaCardContainer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            persona: undefined,
            personaList: persona,
            recipes: undefined
        };
    }

    handleChoosePersona = (p) => {
        this.setState({
            persona: p,
            recipes: calculator.getRecipes_Pre(p),
            personaList: undefined
        })
    }

    handleResetPersona = () => {
        this.setState({
            persona: undefined,
            recipes: undefined,
            personaList: persona
        })
    }

    handleSearch = (e) => {
        if (this.state.personaList)
            this.setState({
                personaList: persona.filter(p =>
                    p.name.toLowerCase()
                        .startsWith(e.target.value.toLowerCase()))
            })

        if (this.state.recipes) {
            this.setState({
                recipes: calculator.getRecipes_Pre(this.state.persona).filter(r =>
                    r.sources.map(s => s.name)
                        .some(p => p.toLowerCase().startsWith(e.target.value.toLowerCase())))
            })
        }
    }

    sortByName = (e) => {
        if (e.target.checked)
            this.setState({
                personaList: this.state.personaList.sort((a, b) => {
                    let na = a.name.toLowerCase(),
                        nb = b.name.toLowerCase();

                    if (na < nb) {
                        return -1;
                    }
                    if (na > nb) {
                        return 1;
                    }
                    return 0;
                })
            })
        else
            this.setState({
                personaList: this.state.personaList.sort((a, b) => {
                    let na = a.name.toLowerCase(),
                        nb = b.name.toLowerCase();

                    if (na > nb) {
                        return -1;
                    }
                    if (na < nb) {
                        return 1;
                    }
                    return 0;
                })
            })
    }
    sortByLevel = (e) => {
        if (e.target.checked)
            this.setState({ personaList: this.state.personaList.sort((a, b) => a.level - b.level) })
        else
            this.setState({ personaList: this.state.personaList.sort((a, b) => b.level - a.level) })

    }
    sortByArcana = (e) => {
        if (e.target.checked)
            this.setState({
                personaList: this.state.personaList.sort((a, b) => {
                    let na = a.arcana,
                        nb = b.arcana;

                    if (calculator.arcanaRank[na] < calculator.arcanaRank[nb]) {
                        return -1;
                    }
                    if (calculator.arcanaRank[na] > calculator.arcanaRank[nb]) {
                        return 1;
                    }
                    return 0;
                })
            })
        else
            this.setState({
                personaList: this.state.personaList.sort((a, b) => {
                    let na = a.arcana,
                        nb = b.arcana;

                    if (calculator.arcanaRank[na] > calculator.arcanaRank[nb]) {
                        return -1;
                    }
                    if (calculator.arcanaRank[na] < calculator.arcanaRank[nb]) {
                        return 1;
                    }
                    return 0;
                })
            })
    }

    noRecipe = (
        <div className="container-fluid py-5 text-center">
            <h3>No recipe found</h3>
        </div>
    )

    render() {
        const customColor = {
            '--bs-link-color': this.props.primaryColor,
            '--bs-primary': this.props.primaryColor,
            '--bs-primary-rgb': this.props.primaryColorRGB,
            '--bs-btn-color': this.props.primaryColor,
            '--bs-btn-border-color': this.props.primaryColor,
            '--bs-btn-hover-bg': this.props.primaryColor,
            '--bs-btn-hover-border-color': this.props.primaryColor,
            '--bs-btn-focus-shadow-rgb': this.props.primaryColorRGB,
            '--bs-btn-active-bg': this.props.primaryColor,
            '--bs-btn-active-border-color': this.props.primaryColor,
            '--bs-btn-disabled-color': this.props.primaryColor,
            '--bs-btn-disabled-border-color': this.props.primaryColor,
        }
        if (!this.state.persona) {
            return (
                <div className="container-fluid pad2y" style={customColor}>
                    <h3 className="text-primary my-4">Recipe Generator</h3>
                    <div className="row">
                        <div className="col input-group mb-3">
                            <span className="input-group-text" id="basic-addon1">
                                <span className="material-symbols-outlined text-black-50" style={{ verticalAlign: 'middle' }}>
                                    search
                                </span>
                            </span>
                            <input type="text"
                                className="form-control"
                                placeholder="Persona Name"
                                aria-label="PersonaName"
                                aria-describedby="button-addon2"
                                onChange={this.handleSearch}
                            />
                        </div>
                        <div className="col-12 d-sm-none d-block mb-2">
                            <div>Sort by:</div>
                            <div className="btn-group w-100" role="group" aria-label="Sorting">
                                <input type="radio" className="btn-check" name="sorting" id="name" autoComplete="off" />
                                <ToggleLabel style={customColor} className="btn btn-outline-primary" htmlFor="name" onChange={this.sortByName}>
                                    Name
                                </ToggleLabel>

                                <input type="radio" className="btn-check" name="sorting" id="level" autoComplete="off" />
                                <ToggleLabel style={customColor} className="btn btn-outline-primary" htmlFor="level" onChange={this.sortByLevel}>
                                    Level
                                </ToggleLabel>

                                <input type="radio" className="btn-check" name="sorting" id="arcana" autoComplete="off" />
                                <ToggleLabel style={customColor} className="btn btn-outline-primary" htmlFor="arcana" onChange={this.sortByArcana}>
                                    Arcana
                                </ToggleLabel>
                            </div>
                        </div>
                    </div >
                    <PersonaTable
                        sortByName={this.sortByName}
                        sortByLevel={this.sortByLevel}
                        sortByArcana={this.sortByArcana}
                        personaList={this.state.personaList}
                        handleChoosePersona={this.handleChoosePersona}
                    />
                </div >
            )
        }
        else {
            let normalRecipes = undefined,
                triangleRecipes = undefined,
                specialRecipes = undefined;
            if (!this.state.persona.special) {
                normalRecipes = this.state.recipes.filter(r => (r.sources.length === 2))
                if (normalRecipes && normalRecipes.length <= 0)
                    normalRecipes = undefined

                triangleRecipes = this.state.recipes.filter(r => (r.sources.length === 3))
                if (triangleRecipes && triangleRecipes.length <= 0)
                    triangleRecipes = undefined
            }
            else {
                specialRecipes = this.state.recipes
            }

            return (
                <div className="container-fluid pad2y" style={customColor}>
                    <h3 className="my-4 text-primary">
                        {'There are ' + this.state.recipes.length
                            + ' recipes for ' + this.state.persona.name}
                    </h3>
                    <div className="row mb-3 g-2">
                        <div className="col-12 col-sm input-group">
                            <span className="input-group-text" id="basic-addon1">
                                <span
                                    className="material-symbols-outlined text-black-50"
                                    style={{ verticalAlign: 'middle' }}>
                                    search
                                </span>
                            </span>
                            <input type="text"
                                className="form-control"
                                placeholder="Ingredient Name"
                                aria-label="IngredientName"
                                aria-describedby="button-addon2"
                                onChange={this.handleSearch}
                            />
                        </div>

                        <div className="col-12 col-sm-2 d-grid">
                            <button
                                className="btn btn-primary rounded-2 bt"
                                onClick={this.handleResetPersona}>
                                Back
                            </button>
                        </div>
                    </div>
                    <ul className="nav nav-tabs nav-justified">
                        <li className="nav-item">
                            <a className="nav-link active" data-bs-toggle="tab" href="#normal">Normal</a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link" data-bs-toggle="tab" href="#triangle">Triangle</a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link" data-bs-toggle="tab" href="#special">Special</a>
                        </li>
                    </ul>
                    <div className="tab-content text-nowrap">
                        <div className="tab-pane fade show active" id="normal">
                            {
                                normalRecipes ?
                                    <RecipeTable recipeList={normalRecipes} />
                                    : this.noRecipe
                            }
                        </div>
                        <div className="tab-pane fade" id="triangle">
                            {
                                triangleRecipes ?
                                    <RecipeTable recipeList={triangleRecipes} />
                                    : this.noRecipe
                            }
                        </div>
                        <div className="tab-pane fade" id="special">
                            {
                                specialRecipes ?
                                    <RecipeTable recipeList={specialRecipes} />
                                    : this.noRecipe
                            }
                        </div>
                    </div>

                </div>
            );
        }
    }
}

function RecipeTable(props) {
    return <div className="table-responsive">
        <table className="table table-responsive table-hover table-striped">
            <thead>
                <tr>
                    <th>#</th>
                    <th>Cost</th>
                    <th>Ingedients</th>
                </tr>
            </thead>
            <tbody className="table-group-divider">
                {
                    props.recipeList.map((recipe, index) =>
                        <tr key={index}>
                            <td>{index + 1}</td>
                            <td>{recipe.cost}</td>
                            <td className="row flex-nowrap container-fluid ms-0 flex-sm-row flex-column">
                                {
                                    recipe.sources.map(persona => {
                                        if (persona.max) {
                                            return <span key={persona.name} className="col" style={{ cursor: "pointer" }} onClick={() => this.handleChoosePersona(persona)}>
                                                {persona.name} <span className="badge bg-secondary">Max</span>
                                            </span>
                                        }
                                        return <span key={persona.name} className="col" style={{ cursor: "pointer" }} onClick={() => this.handleChoosePersona(persona)}>
                                            {persona.name}
                                        </span>
                                    })
                                }
                            </td>
                        </tr>
                    )}
            </tbody>
        </table>
    </div>
}

function PersonaTable(props) {
    return (
        <>
            <div className="table-responsive d-none d-sm-block">
                <table className="table table-bordered table-sm table-hover table-striped">
                    <thead className="bg-primary bg-opacity-10 text-primary">
                        <tr>
                            <th>#</th>
                            <th>
                                <ToggleLabel
                                    className='d-block w-100'
                                    onChange={props.sortByName}
                                >
                                    Name
                                </ToggleLabel>
                            </th>
                            <th>
                                <ToggleLabel
                                    className='d-block w-100'
                                    onChange={props.sortByLevel}
                                >
                                    Level
                                </ToggleLabel>
                            </th>
                            <th>
                                <ToggleLabel
                                    className='d-block w-100'
                                    onChange={props.sortByArcana}
                                >
                                    Arcana
                                </ToggleLabel>
                            </th>
                        </tr>
                    </thead>
                    <tbody className="table-group-divider">
                        {
                            props.personaList.map((p, index) => {
                                if (!p.max)
                                    return <tr key={p.name} style={{ cursor: "pointer" }} onClick={() => props.handleChoosePersona(p)}>
                                        <td>{index + 1}</td>
                                        <td>{p.name}</td>
                                        <td>{p.level}</td>
                                        <td>{p.arcana}</td>
                                    </tr>
                                return <tr key={p.name} style={{ cursor: "pointer" }} onClick={() => props.handleChoosePersona(p)}>
                                    <td>{index + 1}</td>
                                    <td>{p.name} <span className="badge bg-secondary">Max</span></td>
                                    <td>{p.level}</td>
                                    <td>{p.arcana}</td>
                                </tr>
                            })
                        }
                    </tbody>
                </table>
            </div>

            {/* display on mobile */}
            <div className="d-flex flex-wrap gap-2 justify-content-evenly d-sm-none">
                {
                    props.personaList.map(p => {
                        if (!p.max)
                            return <div
                                className="card bg-dark bg-opacity-25"
                                key={p.name}
                                style={{ cursor: "pointer", width: '48%' }}
                                onClick={() => props.handleChoosePersona(p)}
                            >
                                <div className="card-body p-2">
                                    <h6 className="card-title">{p.name}</h6>
                                    <div className="card-text">
                                        <div>{'Level ' + p.level}</div>
                                        <div>{p.arcana}</div>
                                    </div>
                                </div>
                            </div>

                        return <div
                            key={p.name}
                            className="card bg-dark bg-opacity-25"
                            style={{ cursor: "pointer", width: '48%' }}
                            onClick={() => props.handleChoosePersona(p)}>
                            <div className="card-body p-2">
                                <h6 className="card-title">{p.name} <span className="badge bg-secondary">Max</span></h6>
                                <div className="card-text">
                                    <div>{'Level ' + p.level}</div>
                                    <div>{p.arcana}</div>
                                </div>
                            </div>
                        </div>

                    })
                }
            </div>
        </>
    );
}

class ToggleLabel extends React.Component {
    constructor(props) {
        super(props);
        this.state = { isToggleOn: true };

        // This binding is necessary to make `this` work in the callback
        this.handleClick = this.handleClick.bind(this);
    }

    style = {
        ...this.props.style,
        userSelect: 'none',
        cursor: "pointer"
    }

    handleClick(e) {
        this.setState(prevState => ({
            isToggleOn: !prevState.isToggleOn
        }));

        if (typeof this.props.onClick === 'function')
            this.props.onClick(e);

        this.handleChange(e);
    }

    handleChange(e) {
        if (typeof this.props.onChange === 'function') {
            e.target.checked = !this.state.isToggleOn
            this.props.onChange(e);
        }

    }

    render() {
        return (
            <label onClick={this.handleClick} {...this.props} style={this.style}>
                {this.props.children}
                <span className="material-symbols-outlined align-middle">
                    {!this.state.isToggleOn ? 'south' : 'north'}
                </span>
            </label>
        );
    }
}

export default PersonaCardContainer;